<template>
    <div class="p-d-flex p-jc-end">
        <div class="p-field-checkbox p-mr-2 p-mt-2">
            <p-checkbox
                v-model="selectAll"
                :binary="true"
                @change="selectAllChangeHandler"
            />
            <label for="">Select All</label>
        </div>
        <p-button
            label="Archive"
            class="p-mr-2"
            @click="archiveMessageHandler"
        />
        <p-button label="Appointment Stat" />
    </div>

    <p-fieldset legend="All Messages">
        <p-data-table
            :value="messages"
            responsiveLayout="scroll"
            showGridlines
            class="p-datatable-sm p-mb-1"
            :loading="loading"
            dataKey="id"
            selectionMode="single"
            v-model:selection="selectedMessages"
            @row-dblclick="redirectToMessage"
            :rowClass="getRowClass"
        >
            <p-column
                selectionMode="multiple"
                headerStyle="width: 3em"
            ></p-column>
            <p-column header="Date" field="sentdate" style="width: 150px" />
            <p-column header="Subject" field="subject" />
            <p-column header="Sent by" field="user_name" />
            <p-column header="From" field="sendBy" />
            <p-column header="Message" field="message" />
            <p-column header="#">
                <template #body="{data}">
                    <p-badge v-if="data.unRead == 1" />
                </template>
            </p-column>
            <p-column header="Action" field="action" style="width: 150px">
                <template #body="{data}">
                    <p-button
                        label="Reply"
                        class="p-button-sm p-mr-1"
                        @click="openReplyDialog(data)"
                    />
                    <!-- <p-button label="ENG" class="p-button-sm" /> -->
                </template>
            </p-column>
        </p-data-table>
    </p-fieldset>

    <p-dialog
        v-model:visible="replyDialog"
        :closable="false"
        style="width: 600px"
    >
        <p-fieldset legend="Previous Conversation">
            <p-textarea
                v-model="relatedMessages"
                disabled
                rows="10"
                class="p-mt-1"
                style="width: 100%"
                v-if="isMessageThred"
            />
            <p-button
                label="Show Message Thread"
                v-else
                @click="showMessageThread"
            />
        </p-fieldset>
        <p-fieldset :legend="replyTo" class="p-mt-2">
            <div class="p-field">
                <label>Subject - {{ form.subject }}</label>
                <p-textarea
                    v-model="form.message"
                    rows="4"
                    class="p-mt-1"
                    style="width: 100%"
                />
            </div>
        </p-fieldset>

        <template #footer>
            <p-button label="Cancel" @click="closeReplyDialog" />
            <p-button
                v-if="sendSMS"
                label="Mark as Read"
                @click="markReadSMS"
                :loading="loading"
            />
            <p-button
                v-if="!sendSMS"
                label="Send"
                @click="send"
                :loading="loading"
            />
            <p-button
                v-else
                label="Send SMS"
                @click="replyAsSMS"
                :loading="loading"
            />
        </template>
    </p-dialog>
</template>

<script>
import { inject, onMounted, reactive, ref, watch } from "vue";
import {
    getAllMessages,
    composeMessageV2,
    getRelatedMessges,
    archiveMesage,
    archiveSMS
} from "@/service/patient-message-log";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
import { useToast } from "primevue/usetoast";
import { useRouter } from "vue-router";
import { STATSUS } from "@/constants";

import { $axios } from "@/service/app-http";

export default {
    setup() {
        const store = useStore();
        const router = useRouter();
        const messages = ref([]);
        const selectedMessages = ref([]);
        const loading = ref(false);
        const replyDialog = ref(false);
        const selectAll = ref(false);
        const sendSMS = ref(false);

        const form = reactive({
            subject: null,
            message: null,
            sendTo: null,
            sentBy: store.state.user.providerNo,
            senderType: 1,
            parent: null,
            mId: null
        });

        const replyTo = ref();

        const openReplyDialog = (data) => {
            replyTo.value = `Reply To ${data.sendBy}`;

            form.subject = data.subject;
            form.sendTo = data.pid;
            form.parent = data.parent;
            form.mId = data.id;

            replyDialog.value = true;
            relatedMessages.value = data.message;
            isMessageThred.value = false;
        };

        const isMessageThred = ref(false);

        const relatedMessages = ref();
        const showMessageThread = async () => {
            if (form.subject == "Recevied-SMS" && form.parent == 0) {
                isMessageThred.value = true;
                sendSMS.value = true;
            } else {
                isMessageThred.value = true;
                const { relatedMsg } = await getRelatedMessges(form.parent);
                relatedMessages.value = relatedMsg;
                sendSMS.value = false;
            }
        };

        watch(replyDialog, (newVal) => {
            showMessageThread();
        });

        const websocket = inject("websocket");
        const sendLoading = ref(false);

        const replyAsSMS = () => {
            const payload = {
                message: form.message,
                msgId: form.mId
            };

            $axios
                .post("patient/reply-sms", payload)
                .then((response) => {
                    toast.add({
                        severity: "success",
                        summary: "SMS Sent",
                        detail: "SMS sent successfully",
                        life: 3000
                    });

                    getMessages();

                    form.subject = null;
                    form.message = null;
                    replyDialog.value = false;
                    sendLoading.value = false;
                })
                .catch((error) => {
                    toast.add({
                        severity: "error",
                        summary: "Error",
                        detail: "Failed to send SMS",
                        life: 3000
                    });
                });
        };

        const markReadSMS = () => {
            const payload = {
                msgId: form.mId
            };

            $axios
                .post("patient/read-sms", payload)
                .then((response) => {
                    toast.add({
                        severity: "success",
                        summary: "SMS Sent",
                        detail: "SMS marked as read",
                        life: 3000
                    });

                    getMessages();

                    form.subject = null;
                    form.message = null;
                    replyDialog.value = false;
                    sendLoading.value = false;
                })
                .catch((error) => {});
        };

        const send = async () => {
            sendLoading.value = true;
            const { newMessage } = await composeMessageV2({
                ...form
            });

            websocket.send(
                JSON.stringify({
                    sender_id: store.state.user.id,
                    receiver_id: store.state.artCycle.patient.id,
                    terminal: "cycle"
                })
            );

            form.subject = null;
            form.message = null;
            replyDialog.value = false;
            sendLoading.value = false;
            getMessages();
        };

        const getMessages = async () => {
            loading.value = true;
            try {
                const { messageRecords } = await getAllMessages();
                messages.value = messageRecords;
                emitter.emit("message-inbox:get-all-messgae");
                loading.value = false;
            } catch (err) {
                messages.value = [];
                loading.value = false;
            } finally {
                loading.value = false;
            }
        };

        const toast = useToast();

        const setupWebsocket = () => {
            const websocket = inject("websocket");
            websocket.onmessage = (event) => {
                const data = JSON.parse(event.data);
                if (typeof data.data == "string") {
                    const message = JSON.parse(data.data);
                    if (message.terminal == "nlp") {
                        getMessages();
                    }
                }
            };

            websocket.onerror = () => {
                toast.add({
                    severity: "warn",
                    summary: "Chat Server Down",
                    detail: "Instant chat is off at the moment",
                    life: 3000
                });
            };
        };

        const archiveMessageHandler = async () => {
            loading.value = true;
            let ids = selectedMessages.value
                .filter((item) => item.subject !== "Recevied-SMS")
                .map((item) => item.id);

            if (ids.length > 0) {
                let { status } = await archiveMesage(ids);
                getMessages();
            }

            loading.value = true;
            ids = selectedMessages.value
                .filter((item) => item.subject === "Recevied-SMS")
                .map((item) => item.id);
            if (ids.length > 0) {
                let { status2 } = await archiveSMS(ids);
                getMessages();
            }
        };

        const redirectToMessage = (data) => {  
            
            //console.log(data);
            let queryParam = null;

            queryParam = {
                patientId: data.data.pid,
                to: "patient-message-log"
            };

            if (queryParam != null) {
                router.push({
                    name: "redirect",
                    query: queryParam
                });
            }
        };

        onMounted(() => {
            getMessages();
            setupWebsocket();
            emitter.emit("message-nofication:has-seen");
        });

        return {
            messages,
            selectedMessages,
            loading,
            sendSMS,
            replyAsSMS,
            markReadSMS,
            selectAll,
            replyDialog,
            openReplyDialog,
            replyTo,
            isMessageThred,
            relatedMessages,
            showMessageThread,
            form,
            send,
            sendLoading,
            archiveMessageHandler,
            closeReplyDialog: () => {
                replyDialog.value = false;
            },
            selectAllChangeHandler: (event) => {
                if (selectAll.value) {
                    selectedMessages.value = [...messages.value];
                } else {
                    selectedMessages.value = [];
                }
            },
            redirectToMessage,
            getRowClass(data) {
                let resp = {
                    "row-green": data.subject === "Recevied-SMS"
                };

                return resp;
            },        
        };
    }
};
</script>

<style scoped>
:deep() .row-green {
    background-color: #bff1b0 !important;
}
</style>
